 // import popupLayer from '@/components/popup-layer/popup-layer.vue';
 
export default{
	components: {
		// popupLayer
		},
	data(){
		return {
			agree:false,
			// 最近的店铺信息
			nearshop:{ 
				"shopname":"" ,
				"shopaddress":"" ,
				},
				
			// 其他店铺信息
			list:[],
			// boolShow:false 
			// 点击店铺的遮罩
			show:false,
			show2:false,
		}
	},				
	methods:{
		onReachBottom:function(){
			 // 这里向后台请求触底刷新数据
			 console.log('触底了，触发触底事件')
		 },
		//点击显示店铺的事件
		showshop(){
			this.show=true;				
		},
		//点击隐藏店铺的事件
		hidedeatil(){
			this.show=false;
		},
		//点击授权弹框
		showAgree(){
			this.agree = true;
			this.show2 = true
		},
		//点击关闭授权弹框
		closeagree(){
			this.agree = false;
			this.show2 = false
		},
		// 跳订单页
		toList(){
			uni.switchTab({
				url:"../../pages/mannerlist/mannerlist"
			})
		},
		// 跳订单页
		go(){
			uni.switchTab({
				url:"../../pages/mannerlist/mannerlist"
			})
		},
		// 跳订单页
		toOrder(){
			uni.switchTab({
				url:"../../pages/mannerorder/ordertoday"
			})
		},
		// 提示消息
		info(){
			uni.showLoading({
			    title: '敬请期待'
			});
			setTimeout(function () {
			    uni.hideLoading();
			}, 1000);
		},
		/**
		 * 去店铺下单
		 */
		setCurrentShop(index){
		  //1 设置当前店铺到缓存
		  this.$Util.setCache("current_shop",this.list[index]);
		  //2 跳转到菜单页面
		  //跳转导选择店铺的页面
		  uni.switchTab({
		  	url:"../../pages/mannerlist/mannerlist"
		  })
		},
		/**
		 * 
		 * 请求数据
		 * @param {Object} cb
		 */
		async loadData(lng,lat,cb)
		{
			this.$H.get('/mannerdish/index/index?lng='+lng+'&lat='+lat,{},{
				// 这里测试先不用token				
				token:false,
			}).then((res)=>{
				// 最近店铺
				this.data = res
				//店铺列表存为缓存
				this.$Util.setCache("shop_list",res,0);
				this.nearshop.shopname = this.data[0].name
				this.nearshop.shopaddress = this.data[0].address
				this.nearshop.tel = this.data[0].tel
				//删除下标为0，长度为1的一个数据
				let current_shop = this.data.splice(0,1)
				//其他店铺
				this.list = this.data
				//设置最近店铺为当前店铺 如果用户选择其他店铺 要做店铺改变
				this.$Util.setCache("current_shop",current_shop[0],0);
				// console.log(this.list)
				setTimeout(function () {
				    uni.hideLoading();
				}, 1000);
				//执行回调 如果有的话
				cb && cb(res)
			})				
		},			
		onLoad(){												
			//这时候that指的是全局，在内部函数想用全局的时候就得用that
			var that = this;
			// 获取当前坐标
			wx.getLocation({
			    type:'wgs84',
			    success: function (res) {				
					// 打印一下获取的经纬度
			        // console.log('当前位置的经度：' + res.longitude);
			        // console.log('当前位置的纬度：' + res.latitude);
					uni.showLoading({
					    title: '加载中'
					});
					// 请求后台数据
					that.loadData( res.longitude, res.latitude,(res)=>{
					    // 将获取的data数据存入上边的List[]中
					    // that.List = res
					});
			    }
			});
			
		},

	}
}
